@import url('https://fonts.googleapis.com/css2?family=Kumbh+Sans:wght@400;700&display=swap');


button, img, .fa, .count-div, .count {
  -webkit-user-select: none; /* Safari */
  -ms-user-select: none; /* IE 10 and IE 11 */
  user-select: none; /* Standard syntax */
}

body {
  margin: 0 auto;
  font-family: "Kumbh Sans";
}

html, body {
  overflow-x: hidden;
}

#root {
  margin: 0 auto;
  max-width: 1000px;
}

.App {
  margin-inline: 30px;
}

ul {
  display:flex;
  align-items: center;
  padding: 0;
}

li { 
  display: flex;
  align-items: center;
  height: 80px;
  color: gray;
}

header {
  border-bottom: 1px solid rgb(204, 204, 204);
  position: relative;
}

nav {
  display:flex;
  justify-content: space-between;
}

.menu > li {
  margin-right: 30px;
}

header a:hover {

  text-decoration:underline hsl(26, 100%, 55%) 4px;
  text-underline-offset: 46px;
  color: black
}

header a {
  text-decoration: none;
  color: inherit;
}

.avatar {
  width: 50px;
  cursor: pointer;
  border-radius: 100px;
}

.avatar:active {
  outline: 3px solid orange;
}

h1 {
  font-size: 1em;
  color: orange;
  margin: 0;
  text-transform: uppercase;
}

h2 {
  font-size: 2.5em;
  margin: 0;
}

h3 {
  font-size: 1.5em;
  margin-block: 10px;
}

p {
  color: grey;
}

#count {
  display: flex;
}

main {
  margin-block: 70px;
  display: flex;
  justify-content: space-between;
}

.side {
  max-width: clamp(200px, 60%, 430px);
}

.right {
  padding: 50px 20px 50px 20px;
}

.left {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

footer {
  padding: 30px 10px;
  text-align: center;
}

.form {
  display: flex;
}

.price-div {
  display: flex;
  align-items: center;
  flex-direction: column;
  align-items: flex-start;
}

.pricensale {
  display: flex;
  align-items: center;
}

#sale {
  background-color: hsl(25, 100%, 94%);
  border-radius: 6px;
  margin-inline: 10px;
  display: flex;
  align-items: center;
}

#sale > h1 {
  padding: 3px 5px;
}

#count {
  background-color: rgb(244, 244, 244);
  display: flex;
  align-items:center ;
  padding: 10px;
  margin-right: 10px;
  border-radius: 6px;
  font-weight: bold;
  font-size: 1.2em;
}

.count {
  text-align: center;
  padding-inline: 30px;
}

.btn > button {
  background-color:transparent;
  border: none;
  font-size: 1.2em;
  color:hsl(26, 100%, 55%);
  font-weight: bold;
}

.btn > button:active {
color: orange;
}

.add-to-cart {
  background-color: hsl(26, 100%, 55%);
  border: none;
  border-radius: 6px;
  width: 240px;
  color: white;
  font-weight: bold;
  font-size: .9em;
}

.checkout {
  background-color: hsl(26, 100%, 55%);
  border: none;
  border-radius: 6px;
  color: white;
  font-weight: bold;
  font-size: .9em;
  height: 50px;
  width: 100%;
  margin: 10px 0 0 0;
}

.cart-list {
  display: flex;
  flex-direction: column;
}

.container {
  overflow-y: scroll;
  padding: 0;
  width: 100%;
  max-height: 300px;

}

.container > li > img {
  width: 50px;
  border-radius: 6px;
}

.container > li {
  display: flex;
  justify-content: space-between;
  padding: 0 10px 0 10px;
  margin: 0;
}

.info {
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.info > div > p {
  margin-block: 5px 0;
}

.fa-trash-can {
  cursor: pointer;
}

.add-to-cart:active {
  background-color: orange;
}

button:hover {
  cursor: pointer;
}

#was-price {
  text-decoration: line-through;
  margin:0 0 40px 0;
  
}

main img {
  border-radius:10px;
}

.main-image > img {
width: 100%;
margin-bottom: 15px;
}

.thumbnails > img {
  width: 20%;
}

.thumbnails > img:hover {
  cursor: pointer;
  opacity: 0.4;
}

.thumbnails {
  display: flex;
  justify-content: space-between;
}

.thumbnails1 {
  display: flex;
  justify-content: space-between;
  margin-top: 20px;
}

.thumbnails1 > img {
  width: 130px;
  cursor: pointer;
}

.cart-div {
  position: relative;
}

.count-div {
  position: absolute;
  background-color: hsl(26, 100%, 55%);
  border-radius: 10px;
  padding: 1px 10px;
  font-size: 12px;
  color: white;
  top: -12px;
  right: 17px;
}

.left-menu {
  position: fixed;
  background-color: white;
  top: 0;
  left: -300px;
  height: 100%;
  font-weight: 700;
  width: 250px;
  z-index: 10;
  padding-top: 20px;
  padding-left: 20px;
  transition: left 300ms;
}

.left-menu > ul {
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}

.fa-xmark {
  margin-bottom: 40px
}

.fa-xmark:hover {
  transition: rotate 0.5s;
  rotate: 180deg;
  cursor: pointer;
}

.left-menu > ul > li {
  color: black;
padding-block: 0;
  font-size: 1.2em;
  height: 50px;
}

.darken-bg {
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 9;
  background-color: rgba(0, 0, 0, 0.7);
}

.cart {
  position: absolute;
  background-color: white;
  width: 380px;
  top: 100px;
  right: 20px;
  z-index: 8;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.313);
  border-radius: 10px;
}

.cart > p, .cart > ul {
  padding: 2px 25px 2px 25px;
}

.cart > p {
  font-weight: 700;
  color: black;
}

hr {
  border: 1px solid rgba(236, 236, 236, 0.774);
  background-color: transparent;
}

.empty-cart {
  height: 180px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  color: grey
}

.top > img {
width: 100vw;
  margin: 0;
  position: relative;
  border-radius: 0;
  object-fit: cover;

}

.top {
  margin: 0;
  padding: 0;
  max-width: 100vw;
  display: flex;
  position: relative;
  height: clamp(400px, 80vw, 600px);
}

.left-arrow, .right-arrow {
  position: absolute;
  background-color: white;
  border-radius: 100px;
  width: 40px;
  height: 40px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  top: 47%;
  cursor: pointer;
  margin-inline: 15px;
}

.right-arrow {
right: 0
}

.left-arrow1, .right-arrow1 {
  position: absolute;
  background-color: white;
  border-radius: 100px;
  width: 60px;
  height: 60px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  top: 39%;
  cursor: pointer;
  margin-inline: -30px;
}

.left-arrow1:active, .right-arrow1:active {
  color: orange;
}

.right-arrow1 {
right: 0
}

.fa-bars:hover, .fa-cart-shopping {
cursor: pointer;
}

.lightbox {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 10;
  transition: opacity 300ms;
}

.lightbox > img {
  width: 600px;
}

.lightbox-x {
  top: -45px;
  right: 0px;
  position: absolute;
  font-size: 30px;
  color: white;
}

.lightbox-x:active {
  color: orange;
}

@media screen and (max-width: 780px) {

  main {
    flex-direction: column;
    align-items: center;
    margin-top: 0;
    box-sizing: border-box;
  }

  .right {
    padding-inline: 0px;
    max-width: 100%;
  }

  header {
    border: none;
  }

  .avatar {
    width: 35px;
  }

  header a:hover {
    text-decoration: none;
  }

  .form {
    flex-direction: column;
    align-items: center;
    width: 100%;
   
  }
  
  #count {
    justify-content:space-around;
    width: 100%;
    padding: 0;
    margin: 0;
    height: 60px;
  }

  .add-to-cart {
    height: 60px;
    width: 100%;
    margin: 10px 0 0 0;
  }

  .price-div {
    flex-direction: row;
    justify-content: space-between;
  }

  #was-price {
    margin: auto 0;
  }

  .cart {
    left: 50%;
    transform: translateX(-50%);
  }
}